import React, { useRef } from 'react';

import { useHistory } from "react-router"


const ApiAutenticacao = async (history, PayLoadLogin) => await fetch(`${process.env['REACT_APP_BASE_URL']}/Authenticate`,
    {
        method: 'POST',
        headers: new Headers({
            'content-type': 'application/json'
        }),
        body: PayLoadLogin
    })
    .then((x)=> {
        if (x.status === 200) return x.json()
        if (x.status === 401) document.getElementById("login_fail").style.display = "block"
        return
      }
      )
    .then(x => {
        if (x) {

            sessionStorage.setItem("typeUser", x.perfilUsuario)
            sessionStorage.setItem("loginStore", x.user);
            history.push("/");
            return x
        }
    })

async function envia(history, props) {
    const PayLoadLogin = JSON.stringify({
        'login': document.getElementById('login').value,
        'senha': document.getElementById('senha').value
    })
    const retornoAutenticacao = await ApiAutenticacao(history, PayLoadLogin)
    if (retornoAutenticacao !== undefined)
        props.perfil(retornoAutenticacao)
}


function sizeOfThings() {
    var windowHeight = window.innerHeight;
    return windowHeight
};

export default (props) => {
    let history = useHistory()
    const inputEl = useRef(null);

    return (
        <div
            className='row'
            style={{ backgroundColor: 'white',  height: sizeOfThings() - 17  }} >

            <form onSubmit={(event) => {event.preventDefault();envia(history, props)}}>

    

                <div className='col-md-4 offset-md-4' style={{marginTop: '7%'}}>

          

                <div className="alert alert-danger col-md-11"role="alert"id="login_fail"style={{display: "none"}}>
                    Usuário e senha incorretos..
                </div>

                    


                        <div className='row'>


                            <div className='col-md-4 offset-md-3'>
                                <img ref={inputEl} src={`${process.env['REACT_APP_BASE_URL3']}imagem/logo.jpeg`} alt='LOGO BAHM' style={{
                                        padding: 20,
                                        borderRadius: 25,
                                        width: '200px',
                                        height: '100px'
                                    }}/>
                            </div>


                        </div>






                    <div className='row'>

                        <div className='col-md-11'>
                            <input type="text"
                                id="login"
                                name="email"
                                className='form-control'
                                placeholder="Usuário" />

                            <input
                                id="senha"
                                type="password"
                                name="pass"
                                placeholder="Senha"
                                className='form-control'
                                autoComplete='on'
                                style={{
                                    marginTop: '2%'
                                }} />

                            <button style={{
                                backgroundColor: 'blue',
                                color: 'white',
                                padding: 20,
                                borderRadius: 25,
                                marginTop: '10%',
                                width: '100%'
                            }}>
                                ENTRAR
                            </button>

                        </div>
                    </div>
                </div>





            </form>
        </div>
    );
};